.imgCover{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
    
.imgBgBottom {
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
}


//center
.center{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
}

//logo
.logo {
  display:block; /*a*/
  background: url('../images/logo/logo.png') no-repeat; /**/
  width: 190px; /**/
  height: 145px; /**/
  text-indent: 101%; /**/
  overflow: hidden; /**/
  white-space: nowrap; /*""*/
  background-size: cover;
