  // }
  
}

//googleMap
.googleMap {
  overflow: hidden;
   iframe{
       width: 100vw;
       height: 100vh;
      display:block;
  }
}

// loader
.loadPage{
  min-width: 100%;
  min-height: 100%;
  background-color: $white;
  opacity:0.7;
  transition:  3s ease-in-out; 
}
.loadPage,.loader {
	position: fixed;
	top: 50%;
	left: 50%;
  transform: translateX(-50%) translateY(-50%);
	z-index: 10 !important;
}
.ball-beat > div {
  background-color: $primary;
}

// 
.ellipsis5 {
  overflow: hidden;
  display: -webkit-box;
  text-overflow: ellipsis;
  -webkit-line-clamp: 5; /**/
  -webkit-box-orient: vertical;
  white-space: normal;
}

